import { LayoutModule } from "./../commons/layout/layout.module";
import { AuthenticatedGuard } from "./../guards/authenticated.guard";
import { RedirectStudentGuard } from "./../guards/redirect-student.guard";
import { ReportService } from "../services/report.service";
import { UserFeeDialogComponent } from "./../commons/widget/user-fee-dialog/user-fee-dialog.component";
import { LoadingService } from "./../services/loading.service";
import { EditResultDialogComponent } from "./../commons/widget/edit-result-dialog/edit-result-dialog.component";
import {
  MatListModule,
  MatIconModule,
  MatIconRegistry,
  MatCardModule,
  MatMenuModule,
  MatDialogModule,
  MatProgressBarModule,
  MatTableModule
} from "@angular/material";
import { UserService } from "./../services/user.service";
import { ParseService } from "./../services/parse.service";
import { RouteService } from "./../services/route.service";
import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { UserComponent } from "./user.component";
import { RouterModule, Routes } from "@angular/router";
import { WidgetModule } from "../commons/widget/widget.module";
import { ListProfileComponent } from "./profile/profile.component";
import { UsersListComponent } from "./list/list.component";
import { UserNewComponent } from "./new/new.component";
import { UserResolver } from "../resolvers/user";
import { RComponent } from "./r/r.component";
import { AttendanceComponent } from "./attendance/attendance.component";
import { ComposeDialogComponent } from "../commons/widget/compose-dialog/compose-dialog.component";
import { DeleteDialogComponent } from "../commons/widget/delete-dialog/delete-dialog.component";
// import { CdkTableModule } from "@angular/cdk";
import { BasicComponent } from "../commons/layout/basic/basic.component";
import { LinkParentComponent } from "../commons/widget/link-parent/link-parent.component";

export const ROUTES: Routes = [
  {
    path: "",
    component: BasicComponent,
    children: [
      {
        path: "",
        component: UserComponent,
        canActivate: [AuthenticatedGuard],
        canActivateChild: [],
        children: [
          {
            path: ":id",
            component: ListProfileComponent,
            resolve: {
              profile: UserResolver
            },
            children: [
              {
                path: "",
                component: RComponent
              },
              {
                path: "attendance",
                component: AttendanceComponent
              }
            ]
          },
          {
            path: "new",
            component: UserNewComponent
          },
          {
            path: "",
            component: UsersListComponent,
            canActivate: [RedirectStudentGuard]
          }
        ]
      }
    ]
  }
];

@NgModule({
  imports: [
    CommonModule,
    LayoutModule,
    RouterModule.forChild(ROUTES),
    // CdkTableModule,
    MatCardModule,
    MatListModule,
    MatIconModule,
    MatMenuModule,
    MatDialogModule,
    MatTableModule,
    MatProgressBarModule,
    WidgetModule
  ],
  declarations: [
    UserComponent,
    ListProfileComponent,
    UsersListComponent,
    UserNewComponent,
    RComponent,
    AttendanceComponent
  ],
  providers: [UserResolver, ReportService, RedirectStudentGuard],
  entryComponents: [
    ComposeDialogComponent,
    DeleteDialogComponent,
    EditResultDialogComponent,
    UserFeeDialogComponent,
    LinkParentComponent
  ]
})
export class UserModule {}



