import { IReport } from './../../model/IReport';
import { IUser } from './../../model/IUser';
import { AppState } from './../../message/message-state';
import { EditResultDialogComponent } from './../../commons/widget/edit-result-dialog/edit-result-dialog.component';
import { EventType } from './../../model/IEvent';
import { UserService } from './../../services/user.service';
import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { RouteService } from '../../services/route.service';
import { Router, ActivatedRoute } from '@angular/router';
import { IEvent } from '../../model/IEvent';
import { MatDialog } from '@angular/material';
import { DeleteDialogComponent } from '../../commons/widget/delete-dialog/delete-dialog.component';
import { LoadingService } from '../../services/loading.service';
import { ParseService } from '../../services/parse.service';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import { getUserFromState } from '../utils';
import { FetchReportAction } from '../../actions/report-actions';

const Parse = require('parse');

@Component({
  selector: 'sp-r',
  templateUrl: './r.component.html',
  styleUrls: ['./r.component.css']
})
export class RComponent implements OnInit {

  reports: any[] = [];
  dataset: any[] = [];
  user: any;
  editable = this.ps.currentUser.get('role') === 'teacher' || this.ps.currentUser.get('role') === 'admin';
  isLoading = false;

  dialogRef;

  user$: Observable<IUser>;

  reports$: Observable<IReport[]>;

  index = 0;


  constructor(
    private ps: ParseService,
    private ls: LoadingService,
    private store: Store<AppState>,
    private rs: RouteService,
    private route: ActivatedRoute,
    private cdRef: ChangeDetectorRef,
    private dialog: MatDialog,
    private us: UserService,
    private router: Router) { }

  ngOnInit() {
    this.rs.setBc('sticky-header');
    this.ls.setLoading(true);
    this.reports = this.us.getCurrentUser().reports;
    this.user = this.us.getCurrentUser().user.get('user');
    this.createDataset();
    this.ls.setLoading(false);

    // this.user$ = this.store.map(state =>                  state.dataState.users.currentSelectedUserProfile.user);

    // this.user$
    //   .map(user => user)
    //   .flatMap(user => {
    //     const u = this.us.getUserPointer(user.objectId);
    //     return this.us.getResultObservable(u);
    //   })
    //   .map(report => this.getReportItem(report))
    //   .do(report => this.dataset.push)
    //   .subscribe();

  }

  getReportItem(r: any) {
    const item: any = {};
    item.id = this.index + 1;
    item.objectId = r.id;
    item.score = r.get('caScore') + r.get('examScore');
    item.withExam = r.get('withExam');
    item.caScore = r.get('caScore');
    item.examScore = r.get('examScore');
    item.subject = r.get('subject').get('title');
    item.remark = this.getRemark(r.get('score'), item.withExam);

    return r;
  }

  createDataset() {
    for (let index = 0; index < this.reports.length; index++) {
      const element = this.reports[index];
      const item: any = {};
      item.id = index + 1;
      item.objectId = element.id;
      item.score = element.get('caScore') + element.get('examScore');
      item.withExam = element.get('withExam');
      item.caScore = element.get('caScore');
      item.examScore = element.get('examScore');
      item.subject = element.get('subject').get('title');
      item.remark = this.getRemark(element.get('score'), item.withExam);
      this.dataset.push(item);
    }
  }

  getRemark(score: number, we: Boolean): String {
    if (score < 45 && we) {
      return 'Failed';
    } else if (score > 45) {
      return 'Passed';
    } else {
      return 'No Remarks';
    }
  }

  doEvent(e: IEvent) {
    if (e.event === EventType.delete) {
      const deleteDialogRef = this.dialog.open(DeleteDialogComponent);
      deleteDialogRef.afterClosed()
        .filter(r => {
          console.log(r);
          return r === 'delete';
        })
        .flatMap(r => {
          this.isLoading = true;
          return this.us.deleteResult(e.objectId);
        })
        .subscribe(
        (c: any) => {
          console.log(c);
          for (let i = 0; i < this.reports.length; i++) {
            if (this.reports[i].id === e.objectId) {
              this.reports.splice(i, 1);
            }
          }
          this.dataset = [];
          this.createDataset();

          this.isLoading = false;
        },
        err => console.log(err)
        );
    } else if (e.event === EventType.edit) {
      const report = this.reports.filter(i => i.id === e.objectId)[0];
      const editResultDialogRef = this.dialog.open(EditResultDialogComponent, {
        data: report
      });

      editResultDialogRef.afterClosed()
        .filter(r => {
          if (r !== undefined && r !== 'cancel') {
            return r;
          }
          return r !== undefined && r !== 'cancel';
        })
        .flatMap(r => {
          this.isLoading = true;
          report.set('caScore', parseInt(r.caScore, 10));
          report.set('examScore', parseInt(r.examScore, 10));
          report.set('score', parseInt(r.caScore, 10) + parseInt(r.examScore, 10));
          return this.us.saveReport(report);
        })
        .subscribe(
          (d: any) => {
            this.dataset = [];
            this.createDataset();
            this.isLoading = false;
            // console.log(d);
        },
          err => {
            this.isLoading = false;
            console.log(err);
          }
        );
    }

  }

  onClick(e) {
    console.log(e);
  }

}



