import { MomentUtil } from './../../moment-util';
import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/user.service';
import { ParseService } from '../../services/parse.service';

@Component({
  selector: 'sp-attendance',
  templateUrl: './attendance.component.html',
  styleUrls: ['./attendance.component.css']
})
export class AttendanceComponent implements OnInit {

  dataset: any[] = [];

  moment = new MomentUtil().getMoment();

  editable = this.ps.currentUser.get('role') === 'teacher' || this.ps.currentUser.get('role') === 'admin';

  constructor(private us: UserService, private ps: ParseService) { }

  ngOnInit() {
    this.setup(this.us.getCurrentUser().attendance);
  }

  setup(data: any[]) {
    console.log(data);
    data.forEach(element => {
      const a: any = {};
      a.id = element.id;
      a.date = this.moment(element.get('createdAt')).format('MMMM Do YYYY, h:mm:ss a');
      a.status = element.get('status');
      a.markedBy = this.getMarker(element.get('markedBy'));
      this.dataset.push(a);
    });
  }

  getMarker(user): string {
    return user.get('firstName') + ' ' + user.get('lastName');
  }

  onFabClick(e) {

  }

}



