import { LoadingService } from '../../services/loading.service';
import { SetCurrentSelectedUserAction } from './../user-action';
import { IUser } from './../../model/IUser';
import { AppState } from './../../message/message-state';
import { UserFeeDialogComponent } from './../../commons/widget/user-fee-dialog/user-fee-dialog.component';
import { ComposeDialogComponent } from './../../commons/widget/compose-dialog/compose-dialog.component';
import { ParseService } from './../../services/parse.service';
import { UserService } from './../../services/user.service';
import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { RouteService } from '../../services/route.service';
import { MatDialog } from '@angular/material';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import * as _ from 'lodash';
import { getUserFromState } from '../utils';
import { LinkParentComponent } from '../../commons/widget/link-parent/link-parent.component';
import { ToastsManager } from 'ng2-toastr';

@Component({
  selector: 'sp-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ListProfileComponent implements OnInit {

  profile: any;
  isStudent: boolean;
  isParent: boolean;
  isTeacher: boolean;
  isAdmin: boolean;

  user: IUser;

  fees: any;

  id$: Observable<string>;
  id: string;

  constructor(private rs: RouteService,
    private store: Store<AppState>,
    private ls: LoadingService,
    private ps: ParseService,
    private dialog: MatDialog,
    private us: UserService,
    private toastr: ToastsManager,
    vRef: ViewContainerRef,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.toastr.setRootViewContainerRef(vRef);
  }

  ngOnInit() {
    this.rs.setBc('sticky-header');

    this.profile = this.route.snapshot.data['profile'];
    console.log(this.profile);
    this.user = this.us.getUserVM(this.profile.user.get('user'));
    if (!this.user) {
      this.router.navigate(['/404']);
    }
    this.us.setCurrentUser(this.profile);
    this.isStudent = this.user.role === 'user';
    this.isParent = this.user.role === 'parent';
    this.isTeacher = this.user.role === 'teacher';
    this.isAdmin = this.user.role === 'admin';

    if (this.isStudent) {
      if (this.ps.currentUser.get('role') === 'user' && this.user.objectId !== this.ps.currentUser.id) {
        this.router.navigate(['/profile']);
      }
    }

    if (this.isParent) {
      this.router.navigate(['/profile', this.user.objectId]);
    }

    this.us.getFee(this.user.objectId).subscribe((f) => {
      this.fees = f;
    }, err => {
      console.log(err);
    });

  }

  compose() {
    this.dialog.open(ComposeDialogComponent);
    // this.router.navigate(['message', 'compose', this.profile.user.get('user').id]);
  }

  enterFee() {
    const feeRef = this.dialog.open(UserFeeDialogComponent);
  }

  linkParent() {
    const linkRef = this.dialog.open(LinkParentComponent, {
      height: '400px',
      width: '600px',
    });
    linkRef.afterClosed()
      .filter(u => u !== undefined && u.parent !== undefined)
      .do(user => {
        this.toastr.info('Linking Parent');
        this.ls.setLoading(true);
      })
      .flatMap(u => this.us.linkParent(u.parent, this.profile))
      .do(u => {
        this.ls.setLoading(false);
      })
      .subscribe(
        user => {
          console.log('done');
          this.toastr.success('Parent Linked');
        },
        err => {
          console.error(err);
          this.toastr.error(err.message);
        }
      );
  }

}



