import { AppState } from './../../message/message-state';
import { IUser } from './../../model/IUser';
import { IEvent, EventType } from './../../model/IEvent';
import { LoadingService } from './../../services/loading.service';
import { UserService } from './../../services/user.service';
import { RouteService } from './../../services/route.service';
import { ParseService } from './../../services/parse.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { errors } from '../../errors/error-messages';
// import { DataSource } from '@angular/cdk';
import { Observable } from 'rxjs/Rx';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Store } from '@ngrx/store';
import * as _ from 'lodash';

@Component({
  selector: 'sp-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css']
})
export class UsersListComponent implements OnInit {

  users: IUser[] = [];
  currentUser = this.ps.currentUser;

  studentObject: any = {};
  dataset: any[] = [];

  isParent: boolean = this.currentUser.get('role') === 'parent';
  isAdmin: boolean = this.currentUser.get('role') === 'admin';
  isTeacher: boolean = this.currentUser.get('role') === 'teacher';
  isLoading = false;

  constructor(
    private store: Store<AppState>,
    private ls: LoadingService,
    private rs: RouteService,
    private router: Router,
    private ps: ParseService,
    private us: UserService) { }

  ngOnInit() {
    this.rs.setBc('sticky-header');

    if (this.isParent) {
      this.setupParent();
    }

    if (this.isAdmin) {
      this.setupAdmin();
    }

    if (this.isTeacher) {
      this.setupTeacher();
    }

  }

  setupTeacher() {
    this.setupAdmin();
  }

  setupAdmin() {
    // this.us.studentsDatasetObservable.subscribe(
    //   d => this.dataset = d
    // );

    this.us.studentDatasetObservable.subscribe(
      u => this.dataset.push(u)
    );

    let count = 0;

    this.store.map(state => _.values(_.cloneDeep(state.dataState.users.students)))
    .flatMap(u => Observable.from(u))
    .map(user => {
      const item: any = {};
      item.id = count + 1;
      item.objectId = user.objectId;
      item.firstName = user.firstName;
      item.lastName = user.lastName;
      item.sex = user.sex;
      count++;
      return item;
    }).subscribe(
      user => this.us.setStudent(user)
    );

    // this.us.getStudentsCountBySchool().flatMap( c => {
    //   this.studentObject.count = c;
    //   return this.us.getStudentsBySchool();
    // })
  }

  setupParent() {
    this.ls.setLoading(true);

    this.store.subscribe(
      state => {
        this.users = _.values(state.dataState.users.students);
        this.ls.setLoading(false);
      }
    );
  }

  doEvent(e: IEvent) {
    console.log(e);
    if (e.event === EventType.view) {
      this.router.navigate(['user', e.objectId]);
    }
  }

  onClick(e) {
    console.log(e);
  }

}




